import React from "react";
import { useState } from "react";
import axios from "axios";
import Container from "react-bootstrap/Container";
import Button from "react-bootstrap/Button";
import Form from "react-bootstrap/Form";
import parse from 'html-react-parser';

const SearchSolr = () => {
  const [highlights, setHighlights] = useState([]);
  const [highlightsResponse, setHighlightsResponse] = useState([]);

  const [query, setQuery] = useState("");
  const [onSubmitFlag, setOnSubmitFlag]=useState(false);

  const onSubmitHandler = (e) => {
    e.preventDefault();
    console.log("Query:", query);
    let response;
    const getTexts = async () => {
      try {
        /*response = await axios.post("http://localhost:5000/api", {
          query: query,
        });*/

        response = await axios.get(
          "/sanskrit-gallery/granthapp/api/searchingSolrIndex/" + query
        );

        console.log(response.data);
      } catch (error) {
        console.log(error);
      }

      if (response.data["docList"].length !== 0) {
        setHighlights(response.data["docList"]);
	setHighlightsResponse(response.data["results"]);
	setOnSubmitFlag(true);
      } else {
        setHighlights([]);
	setHighlightsResponse([]);
        setOnSubmitFlag(false);
      }
    };
    getTexts();
  };

  

  const printResults = () => {
    const parse = require('html-react-parser');
    console.log("highlights", highlights);
    if (highlights.length === 0 && onSubmitFlag) {
      return <p>फलितांशः नास्ति । </p>;
    } else {
      return (
        <table className="table ">
          <thead className="thead-light">
            <tr>
              <th> # </th>
              <th> फलितांशः (Result)</th>
              <th> मूलम् (Source)</th>
            </tr>
          </thead>
          <tbody>
            {highlights
              .map((item, index) => {
                return (
                  <tr>
			{console.log("item,", item["word"][0])}

		    <td>{index + 1}</td>
                    <td key={index}>{parse(item["word"][0])}</td>
		    <td key={index+"S"}>{highlightsResponse[index]["name"]}</td>
                  </tr>
                );
              })}
          </tbody>
        </table>
      );
    }
  };


  return (
    <>
      <Container className="mt-5">
        <Form onSubmit={onSubmitHandler}>
          <Form.Group className="mb-3">
            <Form.Control
              type="input"
              placeholder="पृच्छा (Query)"
              onChange={(e) => setQuery(e.target.value)}
            />
          </Form.Group>
          <Button
            variant="outline-success"
            type="submit"
            style={{ backgroundColor: "rgb(239, 231, 214)" }}
          >
            अन्वेष्यताम् (Search)
          </Button>
        </Form>
      </Container>
      {highlights.length !== 0 && (
        <Container>
          <p>&nbsp;</p>
          Search Results: {highlights.length}
        </Container>
      )}
      <Container className="mt-3">{printResults()}</Container>
    </>
  );
};

export default SearchSolr;
