import React from "react";
import { useState, useEffect } from "react";
import axios from "axios";
import Container from "react-bootstrap/Container";
import Button from "react-bootstrap/Button";
import Form from "react-bootstrap/Form";

axios.defaults.xsrfCookieName = "csrftoken";
axios.defaults.xsrfHeaderName = "X-CSRFToken";

const ASR = () => {
  const [audioFile, setAudioFile] = useState(null);
  const [submitStatus, setSubmitStatus] = useState(0);
  const [uploadStatus, setUploadStatus] = useState(0);
  const [asrTranscript, setAsrTranscript] = useState("");

  const handleAudioUpload = (event) => {
    setAudioFile(event.target.files[0]);
    console.log("audioFile");
  };

  const handleSubmit = (event) => {
    event.preventDefault();
    setSubmitStatus(submitStatus + 1);
  };

  useEffect(() => {
    const putFileinBackend = async () => {
      try {
        const formData = new FormData();

        formData.append("myFile", audioFile);

        console.log(formData);
        console.log(audioFile);
        try {
          const response = await axios.post(
            "granthapp/api/fileUploadASR/",
            formData
          );

          console.log(response);
        } catch (error) {
          console.log(error);
        }
        setUploadStatus(uploadStatus + 1);
      } catch (err) {
        alert(err);
      }
    };

    async function callerFun() {
      console.log("callerFun");
      console.log("Before putFileinBackend submitStatus", submitStatus);
      await putFileinBackend();
      console.log("Caller");
    }

    callerFun();
  }, [submitStatus]);

  useEffect(() => {
    const getFileinBackend = async () => {
      try {
        const temp = await axios.get("granthapp/api/ASRTranscriptGet/");
        setAsrTranscript(temp.data.transcript);
        console.log("asrTranscript", asrTranscript, asrTranscript.length);
      } catch (err) {
        console.log(err);
      }
    };

    async function callerFun1() {
      console.log("callerFun");
      console.log("Before putFileinBackend submitStatus", uploadStatus);
      await getFileinBackend();
      console.log("Caller");
    }

    callerFun1();
  }, [uploadStatus]);

  return (
    <>
      <Container className="mt-5">
        <Form onSubmit={handleSubmit}>
          <Form.Group controlId="audioForm">
            <Form.Label>Upload Audio</Form.Label>
            <Form.Control
              type="file"
              accept=".mp3,.wav"
              onChange={handleAudioUpload}
            />
          </Form.Group>
          <Button type="submit">Submit</Button>
        </Form>
        {asrTranscript.length ? <div>{asrTranscript}</div> : <></>}
      </Container>
    </>
  );
};

export default ASR;
