import React from "react";
import { useState, useEffect } from "react";
import axios from "axios";
import Container from "react-bootstrap/Container";
import Button from "react-bootstrap/Button";
import Form from "react-bootstrap/Form";
import "./Lexicon.css";

const Lexicon = () => {
  const [files, setFiles] = useState([]);
  const [lexiconWordList, setLexiconWordList] = useState([]);
  const [getTextsubmitStatus, setGetTextsubmitStatus] = useState([]);

  const onSubmitHandler = (e) => {
    e.preventDefault();
    let response;
    const getTexts = async () => {
      try {
        response = await axios.get("granthapp/api/loadLexicon/");
        console.log("datann", response.data["fileList"]);
      } catch (error) {
        console.log(error);
      }

      if (response.data.length !== 0) {
        setFiles(response.data["fileList"]);
      } else {
        setFiles([]);
      }
    };
    getTexts();
  };

  function hideDialog() {
    document.getElementById("popDiv").style.display = "none";
  }

  const handleClick = (e) => {
    console.log("e", e);
    e.preventDefault();

    const id = parseInt(e.target.getAttribute("id"));
    console.log("id", id, typeof id);
    setGetTextsubmitStatus(id);
  };

  useEffect(() => {
    const getText = async () => {
      let response1;

      try {
        response1 = await axios.get(
          "granthapp/api/loadLexiconText/" + files[getTextsubmitStatus]
        );
        console.log("datann", response1.data["lexiconWordList"]);
        if (response1.data.length !== 0) {
          setLexiconWordList(response1.data["lexiconWordList"]);
        } else {
          setLexiconWordList([]);
        }
      } catch (error) {
        console.log(error);
      }
    };

    async function callerFun() {
      console.log("callerFun");
      console.log("Before getText ");
      await getText();
      console.log("Caller");
    }

    callerFun();
  }, [getTextsubmitStatus]);

  useEffect(() => {
    var elementPop = document.getElementById("popDiv");
    if (elementPop) {
      var elementPopParent = elementPop.parentElement;
      elementPopParent.removeChild(elementPop);
    }

    var dialogBoxElement = document.createElement("div");
    dialogBoxElement.setAttribute("id", "popDiv");
    dialogBoxElement.setAttribute("class", "ontop");

    var dialogBoxElementInner = document.createElement("div");
    dialogBoxElementInner.setAttribute("id", "popup");
    console.log(
      "typeof(lexiconWordList)",
      typeof lexiconWordList,
      lexiconWordList
    );
    for (let i = 0; i < lexiconWordList.length; i++) {
      var pElement = document.createElement("p");
      pElement.innerHTML = lexiconWordList[i];
      dialogBoxElementInner.appendChild(pElement);
    }
    //dialogBoxElementInner.setAttribute("class", "ontopInner");
    dialogBoxElement.appendChild(dialogBoxElementInner);

    document.getElementsByTagName("body")[0].appendChild(dialogBoxElement);

    var buttonDialogBoxElement1 = document.createElement("button");
    buttonDialogBoxElement1.setAttribute("class", "btn");
    buttonDialogBoxElement1.classList.add("btn-dark");
    buttonDialogBoxElement1.classList.add("active");
    buttonDialogBoxElement1.innerHTML = "Cancel";
    buttonDialogBoxElement1.style.position = "absolute";
    buttonDialogBoxElement1.style.top = "700px";
    buttonDialogBoxElement1.style.left = "900px";

    dialogBoxElement.appendChild(buttonDialogBoxElement1);

    if (lexiconWordList.length > 0) {
      dialogBoxElement.style.display = "block";
    }

    buttonDialogBoxElement1.addEventListener("click", function (e) {
      hideDialog();
    });
  }, [lexiconWordList]);

  const printResults = () => {
    if (files.length === 0) {
      return <p>No lexicon</p>;
    } else {
      return (
        <table className="table ">
          <thead className="thead-light">
            <tr>
              <th> # </th>
              <th> Filename</th>
            </tr>
          </thead>
          <tbody>
            {files.map((item, index) => {
              return (
                <tr>
                  <td>{index + 1}</td>
                  <td key={index}>
                    <p id={index} onClick={handleClick}>
                      {item}
                    </p>
                  </td>
                </tr>
              );
            })}
          </tbody>
        </table>
      );
    }
  };

  return (
    <>
      <Container className="mt-5">
        <Button
          variant="outline-success"
          type="submit"
          onClick={onSubmitHandler}
          style={{ backgroundColor: "rgb(239, 231, 214)" }}
        >
          Load Lexicon
        </Button>
      </Container>

      <Container className="mt-3">{printResults()}</Container>
    </>
  );
};

export default Lexicon;
